import { UnitRank } from '../../state/army/RegularsState';
import type { RegularsState, UnitRankType } from '../../state/army/RegularsState';

export const MAX_DISTANCE_FROM_REALM = 4;

const maxKill = (n: number, rank: UnitRankType) => {
  switch (rank) {
    case UnitRank.REGULAR:
      return Math.max(Math.ceil(n * 0.1), 60);
    case UnitRank.VETERAN:
      return Math.max(Math.ceil(n * 0.07), 40);
    default:
      return Math.max(Math.ceil(n * 0.05), 20);
  }
};

const nUnits = (units: RegularsState[]) => units.reduce((acc, unit) => acc + unit.count, 0);

export const calcMaxMove = (army: RegularsState[]): number => {
  if (army.length === 0) return 0; // probably only heroes in army move only on player owner lands
  const fullArmy = Object.values(UnitRank).map((r) => ({
    rank: r,
    num: nUnits(army.filter((u) => u.rank === r)),
  }));

  const acc = fullArmy.map((a) => ({ ...a }));
  let i = 1;
  while (i < MAX_DISTANCE_FROM_REALM) {
    Object.values(UnitRank).forEach((rank) => {
      const n = acc.find((a) => a.rank === rank)!;
      n.num = Math.max(n.num - maxKill(n.num, rank), 0);
    });
    if (acc.every((a) => a.num <= 0.4 * fullArmy.find((f) => f.rank === a.rank)!.num)) break;

    i++;
  }

  return Math.max(i, 1);
};
