import { getTreasureItemById, getTurnOwner } from '../../selectors/playerSelectors';
import { getLand, getLandOwner, getTilesInRadius } from '../../selectors/landSelectors';
import { addPlayerLand, removeLandEffect, updateLandEffect, updatePlayer } from '../../systems/gameStateActions';
import { decrementItemCharges, removeEmpireTreasureItem } from '../../systems/playerActions';
import { effectFactory } from '../../factories/effectFactory';
import { getMapDimensions } from '../../utils/screenPositionUtils';
import { applyArmyCasualtiesAtPosition } from './applyArmyCasualties';
import { getValidMagicLands } from './getValidMagicLands';
import { getLandId } from '../../state/map/land/LandId';
import { TreasureName } from '../../types/Treasures';
import { RegularUnitName } from '../../types/UnitType';
import { EffectKind } from '../../types/Effect';
import type { GameState } from '../../state/GameState';
import type { LandPosition } from '../../state/map/land/LandPosition';
import type { PenaltyConfig } from '../../domain/army/armyPenaltyCalculator';

export const invokeItem = (state: GameState, itemId: string, landPos: LandPosition) => {
  const turnOwner = getTurnOwner(state);
  const treasureItem = getTreasureItemById(turnOwner, itemId);

  if (!treasureItem) return; // fallback should never happen

  if (!getValidMagicLands(state, treasureItem.treasure.type).includes(getLandId(landPos))) return;

  // since it is possible to use multiple time the items should not be killer feature
  const penaltyConfig: PenaltyConfig = {
    regular: { minPct: 0, maxPct: 0, minAbs: 30, maxAbs: 60 },
    veteran: { minPct: 0, maxPct: 0, minAbs: 15, maxAbs: 30 },
    elite: { minPct: 0, maxPct: 0, minAbs: 5, maxAbs: 15 },
  };

  if (treasureItem.charge === 0) {
    // remove item from player inventory when user trying to use it after all charges are used
    // it will be a surprise for the user: instead of using item the item scramble it dust and lost forever
    // cover this message in useItemDialog
    Object.assign(state, updatePlayer(state, turnOwner.id, removeEmpireTreasureItem(turnOwner, treasureItem)));
    return;
  }

  let updatedState: GameState = state;
  switch (treasureItem.treasure.type) {
    case TreasureName.WAND_OF_TURN_UNDEAD:
      updatedState = applyArmyCasualtiesAtPosition(updatedState, penaltyConfig, landPos, [RegularUnitName.UNDEAD]);
      break;

    case TreasureName.ORB_OF_STORM:
      updatedState = applyArmyCasualtiesAtPosition(updatedState, penaltyConfig, landPos);
      break;

    case TreasureName.SEED_OF_RENEWAL:
      // todo implement after battle outcome implemented
      break;

    case TreasureName.AEGIS_SHARD:
      updatedState = updateLandEffect(
        updatedState,
        landPos,
        effectFactory(treasureItem.treasure.type, state.turnOwner)
      );
      break;

    case TreasureName.RESURRECTION:
      // todo implement after battle outcome implemented
      break;

    case TreasureName.GLYPH_OF_SEVERANCE:
      // remove one negative effect from the land
      const effectToCancel = getLand(updatedState, landPos).effects.find((e) => e.rules.type === EffectKind.NEGATIVE);
      if (effectToCancel) {
        updatedState = removeLandEffect(updatedState, landPos, effectToCancel.id);
      }
      break;

    case TreasureName.COMPASS_OF_DOMINION:
      const landOwner = getLandOwner(updatedState, landPos);
      // todo when fog of war implemented reveal all visible lands of the player
      const landsToReveal = getTilesInRadius(getMapDimensions(state), landPos, 1, false).filter(
        (l) => getLandOwner(state, l) === landOwner
      );

      landsToReveal.forEach((land) => {
        updatedState = updateLandEffect(updatedState, land, effectFactory(treasureItem.treasure.type, state.turnOwner));
      });
      break;

    case TreasureName.DEED_OF_RECLAMATION:
      // landPos is valid and check in getValidMagicLands
      updatedState = addPlayerLand(updatedState, updatedState.turnOwner, landPos);
      updatedState = updateLandEffect(
        updatedState,
        landPos,
        effectFactory(treasureItem.treasure.type, updatedState.turnOwner)
      );
      break;

    case TreasureName.HOURGLASS_OF_DELAY:
      updatedState = updateLandEffect(
        updatedState,
        landPos,
        effectFactory(TreasureName.HOURGLASS_OF_DELAY, updatedState.turnOwner)
      );
      break;

    // MERCY_OF_ORRIVANE has permanent effect, and it is not necessary to activate it
  }

  // decrement charges
  updatedState = updatePlayer(
    updatedState,
    turnOwner.id,
    decrementItemCharges(getTurnOwner(updatedState), treasureItem)
  );

  Object.assign(state, updatedState);
};
